/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, Image } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import service from '../../../services/product';
import util from '../../../utils/util';
import UploadImage from '../../../utils/UploadImage';
import categoryService from '../../../services/category';

export default function AddOn({ parentId }) {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [category, setCategory] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, parentId, type: 'addOn' });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: '#',
            width: 40,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Name',
            dataIndex: 'name',
            width: 150,
        },
        {
            title: 'Image',
            dataIndex: 'thumbImg',
            width: 80,
            render: (v) => {
                return <Image src={v?.url} height={100} width={150} />
            }
        },
        {
            title: 'MRP',
            dataIndex: 'variants',
            width: 80,
            render: (v) => {
                return v?.map((p) => p?.price)
            }
        },
        {
            title: 'GST',
            dataIndex: 'gst',
            width: 80,
            render: v => v + " %"
        },
        {
            title: 'Inventory',
            dataIndex: 'variants',
            width: 80,
            render: (v) => {
                return v?.map((i) => i?.inventory)
            }
        },
        {
            title: 'Slug',
            dataIndex: 'slug',
            width: 100,
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 60,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this data?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);
    function list() {
        setLoading(true);
        service.listUser(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function listCategory() {
        categoryService.list()
            .then(res => { setCategory(res.data || []); }).catch(err => { AntdMsg('Product data not loaded', 'error'); });
    }

    const deleteData = (id) => {
        service.deleteUser(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }
    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';
    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };
    useEffect(() => {
        list();
        listCategory();
    }, [qData.page, qData.limit]);

    return (
        <>
            {!parentId ? <Typography.Title level={5} style={{ marginTop: 0 }}>Product List</Typography.Title> : null}
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, parentId, category }} />
        </>
    );
};
function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {
    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            delete qData[key];
            list();
        }
    };
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col xs={24} xl={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => onChange(e.target.value, "key")} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected data ?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};
const AddForm = forwardRef((props, ref) => {
    const { list, parentId, } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [thumbFile, setThumbFile] = useState([]);


    const handleChange = (v) => {
        setData({ ...data, ...v });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { parentId, type: "addOn", isPremium: false, isBestSelling: false, forBirthday: false, forAnniversary: false, isBestSellingCombos: false, variants: [{}] }
            );
            if (dt?.thumbFile?.url) {
                setThumbFile([dt?.thumbFile]);
            } else {
                setThumbFile([]);
            }
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detailUser(data._id).then(res => {
            setData(res?.data || {});

            setThumbFile(res?.data?.thumbImg ? [res?.data?.thumbImg] : [])
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }
    const save = () => {
        setAjxRequesting(true);
        service.saveAddOnProduct(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }
    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

	useEffect(() => {
		if (!data._id) {
			handleChange(util.removeSpecialChars(data?.name || ""), "slug");
		}
	}, [data.name]);

    useEffect(() => {
        setData({ ...data, thumbImg: thumbFile[0] })
    }, [thumbFile])

    useEffect(() => {
        if (!open) {
            setData({})
        }
    }, [open])

    return (
        <>
            <Modal
                title={(!data._id ? 'Add New' : 'Edit') + ' AddOn Product'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Name" required>
                                    <Input placeholder="Name" value={data.name}
                                        onChange={(e) => {
                                            setData({
                                                ...data,
                                                name: e.target.value,
                                                variants: [{
                                                    ...data?.variants?.[0],
                                                    weight: e.target.value
                                                }]
                                            })
                                        }}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Slug" required>
                                    <Input placeholder="Slug" value={data.slug} onChange={e => {
                                        handleChange(
                                            util.removeSpecialChars(e.target.value),
                                            "slug"
                                        )
                                    }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select defaultValue={true} value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="MPR">
                                    <Input addonBefore="" placeholder="Price" value={data?.variants?.[0]?.price} onChange={(e) => {
                                        setData({
                                            ...data,
                                            variants: [{
                                                ...data?.variants?.[0],
                                                price: util.handleInteger(e.target.value)
                                            }]
                                        })
                                    }} />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="GST in (%)">
                                    <Input addonBefore="%" placeholder="Ex.- 12" value={data?.gst}
                                        onChange={(e) => {
                                            setData({
                                                ...data,
                                                gst: util.handleInteger(e.target.value)
                                            })
                                        }} />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="Inventory">
                                    <Input placeholder="Inventory" value={data?.variants?.[0]?.inventory}
                                        onChange={(e) => {
                                            setData({
                                                ...data,
                                                variants: [{
                                                    ...data?.variants?.[0],
                                                    inventory: util.handleInteger(e.target.value)
                                                }]
                                            })
                                        }} />
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item label="Thumbnail Photo">
                                    <UploadImage {...{ fileList: thumbFile, setFileList: setThumbFile }} listType="picture" />
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});