import axios from "../utils/axios";

export default class service {

    static baseURL = '';

    static list(data) {
        return axios.get("ecommerce/order/list", { params: data });
    }
    static save(data, query) {
        return axios.post("ecommerce/order/save", data, { params: query });
    }
    static delete(id) {
        return axios.post('ecommerce/order/delete', { ids: id });
    }
    static details(id) {
        return axios.get('ecommerce/order/details/' + id);
    }
}