/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Image, Select, } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import moment from "moment";
import service from '../../services/banner';
import serviceProduct from '../../services/product';
import serviceCategory from '../../services/category';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import UploadImage from '../../utils/UploadImage';


export default function Banner() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [categories, setCategories] = useState([]);
    const [products, setProducts] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, fileType: 'photo' });
    const addNewModalRef = useRef();

    const columns = [
        {
            title: '#',
            width: 20,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Image',
            dataIndex: 'image',
            width: 100,
            render: (v) => {
                return <Image src={v} height={80} />
            }
        },
        {
            title: 'Web Image',
            dataIndex: 'webImg',
            width: 100,
            render: (v) => {
                return <Image src={v} height={80} />
            }
        },
        {
            title: 'Uploaded At',
            dataIndex: 'createdAt',
            width: 100,
            render: (v) => {
                return moment(v).format("dddd, MMMM Do YYYY, h:mm:ss a");
            }
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this image?. Be sure to delete the image from server."
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.bannerList(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.bannerDelete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };


    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    useEffect(() => {
        serviceCategory.list({ isAll: 1 }).then(res => {
            setCategories(res.data || []);
        });
        serviceProduct.listProduct({ isAll: 1, type: "product" }).then(res => {
            setProducts(res.data || []);
        });
    }, []);


    return (
        <>
            <Typography.Title level={5}>List of Banners</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, categories, products }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, categories, products } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);
    const [fileTwoList, setFileTwoList] = useState([]);


    const handleChange = (value) => {
        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { fileType: 'photo' }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.bannerDetails(data._id).then(res => {
            setData(res.data || {});
            setFileList(res.data?.image ? [res.data?.image] : []);
            setFileTwoList(res.data?.webImg ? [res.data?.webImg] : []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        service.bannerSave(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            setOpen(false);
            list();
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setFileList([]);
            setFileTwoList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ image: fileList?.[0] });
    }, [fileList]);
    useEffect(() => {
        handleChange({ webImg: fileTwoList?.[0] });
    }, [fileTwoList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Banner'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Photo">
                                    <UploadImage {...{ fileList, setFileList }} count={1} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Web Image">
                                    <UploadImage {...{ fileList: fileTwoList, setFileList: setFileTwoList }} count={1} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Web Image">
                                    <Select
                                        value={data.pageCode}
                                        onChange={v => {
                                            if (v === "pdp") {
                                                handleChange({ pageCode: v, categoryId: null });
                                            } else if (v === "plp") {
                                                handleChange({ pageCode: v, productId: null });
                                            }
                                        }}
                                        options={[
                                            {
                                                label: "Product Details Page",
                                                value: "pdp"
                                            },
                                            {
                                                label: "Product List Page",
                                                value: "plp"
                                            },
                                        ]}
                                    />
                                </Form.Item>
                            </Col>
                            {
                                data.pageCode === "plp"
                                    ? <Col span={24}>
                                        <Form.Item label="Choose Category">
                                            <Select
                                                value={data.categoryId}
                                                onChange={v => { handleChange({ categoryId: v }) }}
                                                options={categories.map(v => ({ label: v.name, value: v._id }))}
                                            />
                                        </Form.Item>
                                    </Col>
                                    : null
                            }
                            {
                                data.pageCode === "pdp"
                                    ? <Col span={24}>
                                        <Form.Item label="Choose Product">
                                            <Select
                                                value={data.productId}
                                                onChange={v => { handleChange({ productId: v }) }}
                                                options={products.map(v => ({ label: v.name, value: v._id }))}
                                            />
                                        </Form.Item>
                                    </Col>
                                    : null
                            }
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});