import axios from "../utils/axios";

export default class service {

    static baseURL = '';

    static list(data) {
        return axios.get("delivery-boy/list", { params: data });
    }
    static save(data, query) {
        return axios.post("delivery-boy/create", data, { params: query });
    }
    static delete(id) {
        return axios.post('delivery-boy/delete', { ids: id });
    }
    static details(id) {
        return axios.get('delivery-boy/details/' + id);
    }
}