import { Tabs } from 'antd';
import { useState } from 'react';
import List from './List';

export default function Type() {
    const [type, setType] = useState('ocassion');
    return (
        <Tabs
            onChange={(v) => { setType(v) }}
            activeKey={type}
            items={[
                {
                    label: "Occasion",
                    key: 'ocassion',
                    children: <List type={type} />,
                },
                {
                    label: "Recipient",
                    key: 'recipient',
                    children: <List type={type} />
                },
                {
                    label: "Flavour",
                    key: "flavour",
                    children: <List type={type} />
                },
                {
                    label: "Relation",
                    key: "relation",
                    children: <List type={type} />
                }
            ]}
        />
    )
}