/* eslint-disable react-hooks/exhaustive-deps */
import { Button, Form, Row, Col, Spin, Tabs, Card } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import React, { useEffect, useState, } from 'react';
import service from '../../services/application';
import { AntdMsg } from '../../utils/Antd';
import TinyMce from "../../utils/TinyMce"

export default function Application() {

    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [currentTab, setCurrentTab] = useState(0);

    const handleChange = (value, changeForm) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    const save = () => {
        setAjxRequesting(true);
        try {
            (() => {
                
                return currentTab === 0 ? service.save({ ...data, type: 'aboutus', data: data.data }) :
                    currentTab === 1 ? service.save({ ...data, type: 'termcondition', data: data.data }) :
                        currentTab === 2 ? service.save({ ...data, type: 'faq', data: data.data }) :
                            currentTab === 4 ? service.save({ ...data, type: 'privacy-policy', data: data.data }) : null

            })().then(res => {
                AntdMsg("msg", res.message);
                
                handleChange({ ...res.data });
                setCurrentTab((currentTab + 1) % 5);
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        } catch (err) {
            AntdMsg(err.message, 'error');
        }
    }

    const getDetails = (data) => {
        setAjxRequesting(true);
        service.detail(data).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        if (currentTab === 0) {
            getDetails({ _id: data._id, type: "aboutus" });
        } else if (currentTab === 1) {
            getDetails({ _id: data._id, type: "termcondition" });
        } else if (currentTab === 2) {
            getDetails({ _id: data._id, type: "faq" });
        } else if (currentTab === 3) {
            getDetails({ _id: data._id, type: "privacy-policy" });
        }
    }, [currentTab])


    return (
        <>
            <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                <Tabs activeKey={currentTab} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setCurrentTab(v); }}
                    items={[
                        {
                            label: "About us",
                            key: 0,
                            children: <Card style={{ borderTop: 0 }}>
                                <Aboutus {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "Terms & Condition",
                            key: 1,
                            children: <Card style={{ borderTop: 0 }}>
                                <TandC {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "FAQ",
                            key: 2,
                            children: <Card style={{ borderTop: 0 }}>
                                <FAQ {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "Privacy Policy",
                            key: 3,
                            children: <Card style={{ borderTop: 0 }}>
                                <PrivacyPolicy {...{ data, handleChange, save }} />
                            </Card>,
                        }
                    ]}
                />
                <div style={{ display: "flex", flexDirection: "row-reverse", paddingTop: "5px", paddingRight: "70px" }}>
                    <Button key="save" type="primary" onClick={save}>{currentTab === 4 ? "Save" : "Next"}</Button>
                </div>
            </Spin>

        </>
    );
};


const Aboutus = ({ data, handleChange }) => {
    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Data" required>
                            <TinyMce initialValue={data.data === undefined ? "" : data.data} onChange={(v) => { data.data = v }} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

const TandC = ({ data, handleChange }) => {
    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Data" required>
                            <TinyMce initialValue={data.data === undefined ? "" : data.data} onChange={(v) => { data.data = v }} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

const FAQ = ({ data, handleChange }) => {
    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Data" required>
                            <TinyMce initialValue={data.data === undefined ? "" : data.data} onChange={(v) => { data.data = v }} />

                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}
const PrivacyPolicy = ({ data, handleChange }) => {
    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Data" required>
                            <TinyMce initialValue={data.data === undefined ? "" : data.data} onChange={(v) => { data.data = v }} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}