import { Button, Card, Col, Input, Row, Spin } from "antd";
import React, { useEffect, useState } from "react";
import service from "../../services/settings"
import { AntdMsg } from '../../utils/Antd';
import { LoadingOutlined } from '@ant-design/icons';
const initDataMap = [
    {
        type: 'gst',
        title: 'GST Percent',
        dataInput: 'input',
        default: '0',
        iseditable: false
    },
    {
        type: 'referralAmount',
        title: 'Referral Amount',
        dataInput: 'input',
        default: '0',
        iseditable: false
    },
    {
        type: 'phone',
        title: 'Phone Number(Support)',
        dataInput: 'input',
        default: 'xxxxxxxxxx',
        iseditable: false
    },
    {
        type: 'email',
        title: 'email (Support)',
        dataInput: 'input',
        default: 'xxx....@test.com',
        iseditable: false
    },
    {
        type: 'whatsapp',
        title: 'Whatsapp (Support)',
        dataInput: 'input',
        default: 'xxxxxxxxxx',
        iseditable: false
    }
];
const Settings = () => {
    const [data, setData] = useState(initDataMap.map(v => ({ type: v.type })));
    const [dataMap, setDataMap] = useState(initDataMap);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const [ajxRequesting, setAjxRequesting] = useState(false);

    const list = () => {
        setAjxRequesting(true);
        service.list(qData).then((res) => {
            setData(
                initDataMap?.map(v =>
                    res?.data?.find(vv => vv.type === v.type) || { type: v.type, value: v.default }
                )
            )
        }).catch(err => { }).finally(() => {
            setAjxRequesting(false);
        });
    }
    useEffect(() => {
        list()
    }, [])

    const handleSave = (data) => {
        setAjxRequesting(true);
        service.save(data).then((res) => {
            AntdMsg(res.message);
            setDataMap(dataMap.map((vv, ii) => (data.type !== vv.type ? { ...vv } : { ...vv, iseditable: false })))
            list();
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return <>
        <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
            <Row gutter={[12, 12]}>
                {
                    dataMap?.map((val, i) => (
                        <Col span={6} key={i}>
                            <Card
                                title={val.title}
                                size="small"
                                extra={
                                    !val.iseditable ?
                                        <Button type="primary" size="small" onClick={() => setDataMap(dataMap.map((vv, ii) => (i !== ii ? { ...vv } : { ...vv, iseditable: true })))} > Edit </Button> :
                                        <Row gutter={6}>
                                            <Col>
                                                <Button type="primary" size="small" danger onClick={() => setDataMap(dataMap.map((vv, ii) => (i !== ii ? { ...vv } : { ...vv, iseditable: false })))}> Cancel </Button>
                                            </Col>
                                            <Col>
                                                <Button type="primary" size="small" onClick={() => {
                                                    handleSave(data?.find((vv, ii) => vv.type === val.type))
                                                }} > Save </Button>
                                            </Col>
                                        </Row>
                                }
                            >
                                {
                                    val.dataInput === 'input'
                                        ? <Input value={(data?.find((vv, ii) => vv.type === val.type))?.value} onChange={e => {
                                            setData(data.map((vv, ii) => (vv.type === val.type ? { ...vv, value: e.target.value } : { ...vv })))
                                        }} disabled={!val.iseditable} />
                                        : null
                                }
                            </Card>
                        </Col>
                    ))
                }
            </Row>
        </Spin>
    </>
};
export default Settings;