import axios from "../utils/axios";

export default class service {

    static baseURL = 'delivery';

    // for deliveryType : 
    static deliveryTypeDetails(id, data) {
        return axios.get(this.baseURL + '/delivery-type/details/' + id, { params: data });
    }
    static deliveryTypeList(data) {
        return axios.get(this.baseURL + '/delivery-type/list', { params: data });
    }
    static deliveryTypeSave(data, query) {
        return axios.post(this.baseURL + "/delivery-type/save", data, { params: query });
    }
    static deliveryTypeDelete(id) {
        return axios.post(this.baseURL + '/delivery-type/delete', { ids: id });
    }

    // for delivery :
    static deliveryDetails(id, data) {
        return axios.get(this.baseURL + '/details/' + id, { params: data });
    }
    static deliveryList(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static deliverySave(data, query) {
        return axios.post(this.baseURL + "/save", data, { params: query });
    }
    static deliveryDelete(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }
}