/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Input,
  Row,
  Col,
  Popconfirm,
  Descriptions,
  Select,
  DatePicker,
  Typography,
  Tag,
  Spin,
  message,
  Modal,
  Image,
} from "antd";
import {
  EditOutlined,
  LoadingOutlined,
  UserOutlined,
  DeliveredProcedureOutlined,
} from "@ant-design/icons";
import React, {
  forwardRef,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";
import service from "../../services/orders";
import Pagination from "../components/Pagination";
import { AntdMsg } from "../../utils/Antd";
import UserService from "../../services/userList";
import moment from "moment";
import dayjs from "dayjs";
import util from "../../utils/util";
import serviceDelivery from "../../services/deliveryBoy";

export default function Order() {
  const [data, setData] = useState([]);
  const [user, setUser] = useState([]);
  const [loading, setLoading] = useState(false);
  const [qData, setQData] = useState({
    page: 1,
    limit: 20,
    date: new Date(),
    status: "Confirmed",
  });
  const deliveryModalRef = useRef();

  const columns = [
    {
      title: "Order id",
      dataIndex: "orderId",
      width: 300,
    },
    {
      title: "Delivery Assign To",
      dataIndex: "deliveryBoyData",
      width: 140,
      render: (v) => <span>{v[0]?.name}</span>,
    },
    {
      title: "Shipping Address",
      dataIndex: "addressDetails",
      width: 400,
      render: (_) => {
        const data = JSON.parse(_);
        return data !== null ? (
          <span>
            {data?.landMark +
              " " +
              data?.address +
              " " +
              data?.city +
              " " +
              data?.pin}
          </span>
        ) : (
          <span>Not Mentioned</span>
        );
      },
    },
    {
      title: "Total Amount",
      dataIndex: "priceDetails",
      width: 90,
      render: (_) => {
        return <span>{_.finalPrice}</span>;
      },
    },

    {
      title: "OrderedAt",
      dataIndex: "createdAt",
      width: 180,
      render: (v) => {
        return moment(v).format("dddd, MMMM Do YYYY");
      },
    },
    {
      title: "Status",
      dataIndex: "status",
      width: 70,
      render: (_, { status }) => {
        return <Tag color="green">{status}</Tag>;
      },
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      render: (v, row) => {
        return (
          <>
            <Button
              type="primary"
              size="small"
              icon={<UserOutlined />}
              onClick={() => {
                deliveryModalRef.current.openForm(row);
              }}
            />
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);

  function list() {
    setLoading(true);
    service
      .list(qData)
      .then((res) => {
        setData(res.data?.map((v) => ({ ...v, key: v._id })));
        setQData({
          ...qData,
          limit: res.extra.limit,
          page: res.extra.page,
          total: res.extra.total,
        });
      })
      .catch((err) => {})
      .finally(() => {
        setLoading(false);
      });
  }

  function listUser() {
    UserService.listUser()
      .then((res) => {
        setUser(res.data || []);
      })
      .catch((err) => {
        AntdMsg("User data not loaded", "error");
      });
  }
  const deleteData = (id) => {
    service
      .delete(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };

  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";


  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search {...{ deliveryModalRef, deleteData, qData, setQData, list,user }} />
    ),

    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    tableLayout: undefined,
  };

  useEffect(() => {
    list();
    listUser();
  }, [qData.page, qData.limit]);
  return (
    <>
      <Typography.Title level={5}>List of Orders</Typography.Title>

      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
        expandable={{
          expandedRowRender: (record) => (
            <>
              <Descriptions title="Receiver Info">
                <Descriptions.Item label="UserName">
                  {record?.userDetails
                    ? record?.userDetails.name
                    : "Not Accepted"}
                </Descriptions.Item>
                <Descriptions.Item label="Email">
                  {record?.userDetails ? record?.userDetails.email : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Phone">
                  {record?.userDetails ? record?.userDetails.phone : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Gender">
                  {record?.userDetails ? record?.userDetails.gender : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Address">
                  {record?.addressDetails !== null ? (
                    <span>
                      {JSON.parse(record?.addressDetails)?.landMark +
                        "" +
                        JSON.parse(record?.addressDetails)?.address +
                        " , " +
                        JSON.parse(record?.addressDetails)?.city}
                    </span>
                  ) : (
                    <span>Not Mentioned</span>
                  )}
                </Descriptions.Item>
              </Descriptions>
              <Descriptions title="Ordered Items"></Descriptions>
              <div style={{ display: "flex" }}>
                <Table
                  // width={300}
                  bordered
                  // style={{width: '500px'}}
                  pagination={false}
                  size="small"
                  dataSource={[record]}
                  columns={[
                    {
                      dataIndex: "orders",
                      width: 1400,
                      render: (v, i) => (
                        <>
                          <div>
                            <span style={{ margin: "0 10px" }}>Item No.</span>
                            <span style={{ margin: "0 70px" }}>
                              Product Name
                            </span>
                            <span style={{ margin: "0 20px" }}>
                              Variant Name
                            </span>
                            <span style={{ margin: "0 10px" }}>Quantity</span>
                            <span style={{ margin: "0 40px" }}>Price</span>
                            <span style={{ margin: "0 40px" }}>
                              Offer/Disc.
                            </span>
                            <span style={{ margin: "0 40px" }}>
                              Cake Message
                            </span>
                            <span style={{ margin: "0 40px" }}>
                              Heart Shape
                            </span>
                            <span style={{ margin: "0 40px" }}>
                              Extra
                            </span>
                            <span style={{ margin: "0 40px" }}>
                              Cake Photo
                            </span>
                          </div>
                          {v?.map((vv, i) => (
                            <div>
                              <div>
                                <span
                                  style={{
                                    margin: "0 15px",
                                    display: "inline-block",
                                    width: "60px",
                                  }}
                                >
                                  {i + 1}.
                                </span>
                                <span
                                  style={{
                                    margin: "0 40px",
                                    display: "inline-block",
                                    width: "150px",
                                  }}
                                >
                                  {record?.ProductDetails?.map((prod) => {
                                    return prod?.variants?.map((vari) => {
                                      if (vari?._id === vv?.productVariantId) {
                                        return prod?.name;
                                      }
                                    });
                                  })}
                                </span>
                                <span
                                  style={{
                                    margin: "0 10px",
                                    display: "inline-block",
                                    width: "100px",
                                  }}
                                >
                                  {record?.ProductDetails?.map((prod) => {
                                    return prod?.variants?.map((vari) => {
                                      if (vari?._id === vv?.productVariantId) {
                                        return (
                                          <Tag color="green">
                                            {vari?.weight}
                                          </Tag>
                                        );
                                      }
                                    });
                                  })}
                                </span>
                                <span
                                  style={{
                                    margin: "0 30px",
                                    display: "inline-block",
                                    width: "10px",
                                  }}
                                >
                                  {vv?.quantity}
                                </span>
                                <span style={{ margin: "0 40px" }}>
                                {vv?.price}
                                </span>
                                <span style={{ margin: "0 40px" }}>
                                  <Tag color="red">NULL</Tag>
                                </span>
                                <span style={{ margin: "0 30px" }}>
                                  <span>{vv?.cakeMessage || "No Message"}</span>
                                </span>
                                <span style={{ margin: "0 80px 0 100px" }}>
                                  <Tag color={record?.isHeart === true ? "green" : "red"}>{vv?.isHeart === true ? "Yes" : "No"}</Tag>
                                </span>
                                <span style={{ margin: "0 50px 0 10px" }}>
                                  {record?.isHeart === true ? "+100" : "0"}
                                </span>
                                <span style={{ margin: "0 40px" }}>
                                  {vv?.photoURL?.url ? <Image src={vv?.photoURL?.url} height={80}/> :  "No Image"}
                                </span>
                              </div>
                              <hr />
                            </div>
                          ))}
                          <div
                            style={{
                              display: "flex",
                              flexDirection: "row-reverse",
                              width: "650px"
                            }}
                          >
                            <div style={{marginLeft:"130px"}}>
                              <div>
                                <span
                                  style={{
                                    margin: "0 5px",
                                    display: "inline-block",
                                    width: "70px",
                                  }}
                                >
                                  <b>GST</b>
                                </span>
                                <span style={{ margin: "0 30px",marginRight:"60px" }}>
                                  {" "}
                                  {record?.priceDetails?.gst}{" "}
                                </span>
                              </div>
                              <div>
                                <span
                                  style={{
                                    margin: "0 5px",
                                    display: "inline-block",
                                    width: "70px",
                                  }}
                                >
                                  <b>Final Price</b>
                                </span>
                                <span style={{ margin: "0 30px",marginRight:"60px" }}>
                                  {" "}
                                  {record?.priceDetails?.finalPrice}{" "}
                                </span>
                              </div>
                              <div>
                                <span
                                  style={{
                                    margin: "0 5px",
                                    display: "inline-block",
                                    width: "70px",
                                  }}
                                >
                                  <b>Discount</b>
                                </span>
                                <span style={{ margin: "0 30px",marginRight:"60px" }}>
                                  {" "}
                                  {record?.priceDetails?.discountPrice}{" "}
                                </span>
                              </div>
                              <div>
                                <span
                                  style={{
                                    margin: "0 5px",
                                    display: "inline-block",
                                    width: "70px",
                                  }}
                                >
                                  <b>Delivery</b>
                                </span>
                                <span style={{ margin: "0 30px",marginRight:"60px" }}>
                                  {" "}
                                  {record?.priceDetails?.delivery}{" "}
                                </span>
                              </div>
                              <div>
                                <span
                                  style={{
                                    margin: "0 5px",
                                    display: "inline-block",
                                    width: "70px",
                                  }}
                                >
                                  <b>Coupon</b>
                                </span>
                                <span style={{ margin: "0 30px",marginRight:"60px" }}>
                                  {" "}
                                  {record?.priceDetails?.couponDiscountPrice}{" "}
                                </span>
                              </div>
                              <div>
                                <span
                                  style={{
                                    margin: "0 5px",
                                    display: "inline-block",
                                    width: "70px",
                                  }}
                                >
                                  <b>Total</b>
                                </span>
                                <span style={{ margin: "0 30px",marginRight:"60px" }}>
                                  {" "}
                                  {record?.priceDetails?.finalPrice}{" "}
                                </span>
                              </div>
                            </div>
                          </div>
                        </>
                      ),
                    },
                  ]}
                />
              </div>
            </>
          ),
          rowExpandable: (record) => record.userDetails !== "Not Expandable",
        }}
      />
      <DeliveryForm
        ref={deliveryModalRef}
        {...{
          list,
        }}
      />
    </>
  );
}

function Search({ deleteData, qData, setQData, list, user }) {
  const [userNames, setUserNames] = useState([]);

  const onChange = (v, key) => {
    qData[key] = v;
    if (v === undefined || v === "") {
      qData[key] = "";
      list();
    }
  };
  useEffect(() => {
    const Names = user?.map((item) => ({
      label: item.name ? (
        <>
          <p style={{ padding: "0px", margin: "0px" }}>{item.name}</p>
          <p style={{ fontSize: "12px", margin: "0px" }}>
            <Typography.Text type="danger">@{item.phone}</Typography.Text>
          </p>
        </>
      ) : (
        item.phone
      ),
      value: item._id,
      searchKey: item.name + item.phone,
    }));
    setUserNames(Names);
  }, [user]);

  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col span={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by order id"
              onChange={(e) => onChange(e.target.value, "key")}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col xs={24} xl={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Select
              showSearch
              allowClear
              placeholder="Select by username / phone"
              optionFilterProp="children"
              onChange={(e) => onChange(e, "userId")}
              filterOption={(input, option) =>
                (option?.searchKey ?? "")
                  .toLowerCase()
                  .includes(input.toLowerCase())
              }
              options={userNames}
            />
          </Form.Item>
        </Col>

        <Col xs={24} xl={4}>
          <Form.Item>
            <Select
              defaultValue={"Initiated"}
              allowClear
              placeholder="Order Status"
              onChange={(e) => onChange(e, "status")}
            >
            <Select.Option value={"Initiated"}>Initiated</Select.Option>
            <Select.Option value={"Confirmed"}>Confirmed</Select.Option>
            <Select.Option value={"Delivered"}>Delivered</Select.Option>
            <Select.Option value={"Failed"}>Failed</Select.Option>
            <Select.Option value={"Pending"}>Pending</Select.Option>
            <Select.Option value={"Shipped"}>Shipped</Select.Option>
            </Select>
          </Form.Item>
        </Col>
        <Col xs={24} xl={4}>
          <Form.Item>
            <DatePicker
              defaultValue={dayjs(moment(qData?.date).format("YYYY-MM-DD"))}
              allowClear
              onChange={(e) => onChange(e, "date")}
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

const DeliveryForm = forwardRef((props, ref) => {
  const { list, categoryId } = props;

  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  const [ajxRequesting, setAjxRequesting] = useState(false);
  const [thumbFile, setThumbFile] = useState([]);
  const [file, setFile] = useState([]);
  const [deliveryBoys, setDeliveryBoys] = useState([]);

  const handleChange = (v, k) => {
    let varDt = data;
    let keys = k.split(".");
    for (let i = 0; i < keys.length; i++) {
      if (i + 1 === keys.length) {
        varDt[keys[i]] = v;
      } else {
        if (typeof varDt[keys[i]] === "undefined") {
          if (parseInt(keys[i + 1]) * 1 >= 0) {
            varDt[keys[i]] = [];
          } else {
            varDt[keys[i]] = {};
          }
        }
        varDt = varDt[keys[i]];
      }
    }
    setData({ ...data, ...v });
  };

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(
        dt
          ? {
              ...dt,
              ocassion: [],
              flavour: [],
              recipient: [],
              relation: [],
              specializationIds: [],
            }
          : {
              isPremium: false,
              isBestSelling: false,
              forBirthday: false,
              forAnniversary: false,
              isBestSellingCombos: false,
              variants: [{}],
              ocassion: [],
              flavour: [],
              recipient: [],
              relation: [],
              specializationIds: [],
              categoryIds: [categoryId],
            }
      );
    },
  }));

  function listDeliveryBoys() {
    setAjxRequesting(true);
    serviceDelivery
      .list({ status: "active" })
      .then((res) => {
        setDeliveryBoys(res.data);
      })
      .catch((err) => {})
      .finally(() => {
        setAjxRequesting(false);
      });
  }
  const save = () => {
    setAjxRequesting(true);
    service
      .save(data)
      .then((res) => {
        console.log(res.data);
        message.success(res.message);
        list();
        setOpen(false);
      })
      .catch((err) => {
        if (typeof err.message === "object") {
          let dt = err.message[Object.keys(err.message)[0]];
          message.error(dt);
        } else {
          AntdMsg(err.message, "error");
        }
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };

  useEffect(() => {
    listDeliveryBoys();
  }, [open]);

  return (
    <>
      <Modal
        title={(!data._id ? "Add New" : "Edit") + " Delivery Boy"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={700}
        height={500}
        className="app-modal-body"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          <Button key="save" type="primary" onClick={save}>
            Save
          </Button>,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          <Form layout="vertical">
            <Col span={12}>
              <Form.Item label="Delivery Assigned To" required>
                <Select
                  mode="single"
                  value={data.deliveryBoyId}
                  onChange={(e) => {
                    setData({...data,deliveryBoyId: e})
                  }}
                  options={deliveryBoys?.map((v) => ({
                    value: v._id,
                    label: v.name,
                  }))}
                ></Select>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item label="Status" required>
                <Select
                mode="single"
                  value={data.status}
                  onChange={(e) => {
                    setData({...data,status: e})
                  }}
                >
                  <Select.Option value={"Initiated"}>Initiated</Select.Option>
                  <Select.Option value={"Confirmed"}>Confirmed</Select.Option>
                  <Select.Option value={"Delivered"}>Delivered</Select.Option>
                  <Select.Option value={"Failed"}>Failed</Select.Option>
                  <Select.Option value={"Pending"}>Pending</Select.Option>
                  <Select.Option value={"Shipped"}>Shipped</Select.Option>
                </Select>
              </Form.Item>
            </Col>
          </Form>
        </Spin>
      </Modal>
    </>
  );
});

