/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, TimePicker } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined, CloseOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import dayjs from 'dayjs';
import deliveryService from '../../services/delivery';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';


export default function Delivery() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const addNewModalRef = useRef();

    const columns = [
        {
            title: '#',
            width: 20,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Name',
            dataIndex: 'name',
            width: 80,
        },
        {
            title: 'Charge',
            dataIndex: 'charge',
            width: 80,
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete?. Be sure to delete the data from server."
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        deliveryService.deliveryList(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }


    const deleteData = (id) => {
        deliveryService.deliveryDelete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };


    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);


    return (
        <>
            <Typography.Title level={5}>List of Delivery</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {
    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            delete qData[key];
            list();
        }
    };
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col
                    xs={{ span: 24 }}
                    sm={{ span: 16 }}
                    md={{ span: 10 }}
                    lg={{ span: 4 }}
                >
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by title" value={qData.key} onChange={e => onChange(e.target.value, "key")} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete?. Be sure to delete the data from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);


    const handleChange = (value) => {
        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { status: true, timings: [{ startTime: "00:00:00", endTime: "00:00:00" }] }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        deliveryService.deliverySave(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Delivery'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Name" required>
                                    <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Charge" required>
                                    <Input type='number' placeholder="Charge" value={data.charge} onChange={e => { handleChange({ charge: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Timing {...{ timingDetails: data.timings, parentKay: 'timings', handleChange }} />

                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={v => { handleChange({ status: v }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});

function Timing({ timingDetails: data, parentKay, handleChange }) {
    return (
        <>
            <Table
                {...{
                    bordered: true,
                    size: 'small',
                    showHeader: true,
                    footer: null,
                    tableLayout: undefined,
                }}
                pagination={{ position: ['none'] }}
                columns={
                    [
                        {
                            title: 'Start Time',
                            dataIndex: 'startTime',
                            render: (row, v, i) => <TimePicker onChange={(e, timeString) => { handleChange({ [`${parentKay}.${i}.startTime`]: timeString }) }} defaultValue={dayjs(v?.startTime, 'HH:mm:ss')} />
                        },
                        {
                            title: 'End Time',
                            dataIndex: 'endTime',
                            render: (row, v, i) => <TimePicker onChange={(e, timeString) => { handleChange({ [`${parentKay}.${i}.endTime`]: timeString }) }} defaultValue={dayjs(v?.endTime, 'HH:mm:ss')} />
                        },
                        {
                            title: 'Action',
                            dataIndex: 'price',
                            width: 100,
                            render: (v, row, i) => {
                                return <>
                                    {
                                        i + 1 === data.length
                                            ? <Button icon={<PlusOutlined />} shape="circle" type="dashed" onClick={() => {
                                                handleChange({ [parentKay]: [...data, { startTime: "00:00:00", endTime: "00:00:00" }] });
                                            }} />
                                            : null
                                    }
                                    {
                                        i + 1 === data.length && i !== 0
                                            ? <Button icon={<CloseOutlined />} shape="circle" type="dashed" className="mx-2" danger onClick={() => {
                                                data.pop();
                                                // handleChange({[parentKay]: [...data.filter((v, i)=> (i+1) !== data.length)]} );
                                                handleChange({[parentKay]: [...data]} );
                                            }} />
                                            : null
                                    }
                                </>
                            }
                        }
                    ]
                }
                dataSource={data?.length ? data : []}
            />
        </>
    );
}